import React, { Component } from 'react';
import Chart from 'react-apexcharts';

const font = {
    name: {size: '12px'}, value: {size: '32px'},
    fontFamily: 'Nunito, Arial, sans-serif'
};

const gauge = {
    startAngle: -135,
    endAngle: 135
};

class ApexChartGauge extends Component {
    constructor({chartLabel, chartHeight, ...props}) {
        
        super(props);
        this.chartHeight = chartHeight;
        this.state = {
            series: [43.12],
            options: {
                series: [0],
                labels: [chartLabel],
                chart: {
                    height: chartHeight,
                    type: 'radialBar',
                    toolbar: {
                        show: false
                    }
                },

                plotOptions: {
                    radialBar: {
                        startAngle: gauge.startAngle,
                        endAngle: gauge.endAngle,
                        hollow: {
                            size: '67',
                            position: 'front'
                        },
                        track: {
                            margin: 0,
                            dropShadow: {
                                enabled: true,
                                opacity: 0.2,
                                top: -3,
                                left: 0,
                                blur: 4
                            }
                        },
            
                        dataLabels: {
                            showOn: 'always',
                            name: {
                                offsetY: -10,
                                fontFamily: font.fontFamily,
                                fontSize: font.name.size,
                                color: '#858796',
                                show: true
                            },
                            value: {
                                formatter: (value) => parseFloat (value),
                                fontFamily: font.fontFamily,
                                fontSize: font.value.size,
                                color: '#858796',
                                show: true
                            }
                        }
                    }
                },
                fill: {
                    colors: [
                        function ({value}) {
                            if (value < 75) return '#2ECC71';
                            else if (value > 74 && value < 90) return '#F39C12';
                            else return '#E74C3C';
                        }
                    ],

                    opacity: (1),
                    type: 'gradient',
                    gradient: {
                        shade: 'dark',
                        type: 'vertical',
                        gradientToColors: ['#40739E'],
                        inverseColors: (false),
                        stops: [0, 100]
                    }
                },
                stroke: {
                    lineCap: 'round'
                }
            }
        };
    };
    render() {
        return (
            <div className="d-flex align-items-top justify-content-center">
                <Chart options={this.state.options} series={this.state.series} type="radialBar"
                    height={this.chartHeight}/>
            </div>
        );
    };
};

export default ApexChartGauge;